@charset "UTF-8";
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  font-family: '微軟正黑體','Nunito', sans-serif; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul, li {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.login-wrapper {
  display: flex;
  flex-direction: column;
  padding-top: 10rem;
  align-items: center;
  height: 100vh;
  background: #eaebec; }
  .login-wrapper .bookicon {
    font-size: 80px;
    margin-bottom: 20px;
    color: #686a6a; }
  .login-wrapper h3 {
    margin-bottom: 1.3rem;
    font-size: 24px; }
  .login-wrapper .login-box {
    width: 350px;
    padding: 1.5rem 1.5rem 2rem 1.5rem; }

.header {
  background-color: white;
  width: 100%;
  height: 70px;
  color: #4a4a4a;
  font-size: 11px;
  line-height: 65px;
  font-size: 16px; }
  .header .logo {
    display: inline-block;
    color: #072230;
    background: url("/images/logo.png");
    width: 200px;
    height: 53px;
    text-indent: -99999px; }
  .header .level-right a {
    margin-left: 10px; }
    .header .level-right a i {
      margin-right: 5px; }
  .header .addBook {
    margin-left: 10px; }

.search {
  background: #ccc;
  height: 300px;
  background: url("/images/search-bg.jpg") center center no-repeat;
  width: 100%;
  background-size: cover; }
  .search .container {
    padding-top: 80px;
    text-align: center;
    color: #fff;
    letter-spacing: 2px; }
    .search .container h2 {
      font-size: 25px;
      margin-bottom: 10px; }
    .search .container .searchInput {
      justify-content: center;
      margin-top: 18px; }
      .search .container .searchInput input {
        width: 500px; }

.Products {
  padding: 30px 0; }

.productItem .image {
  width: 150px;
  position: relative; }
  .productItem .image .sold-out {
    background: rgba(255, 255, 255, 0.85);
    position: absolute;
    height: 100%;
    width: 100%;
    text-align: center;
    color: #d15e56;
    font-weight: bolder; }
    .productItem .image .sold-out i {
      padding: 40px 0 15px 0;
      font-size: 40px; }
  .productItem .image .sold-out.hide {
    display: none; }

.productItem li {
  margin-bottom: 5px; }

.productItem .bookInfo {
  position: relative; }
  .productItem .bookInfo ul {
    width: 225px; }
  .productItem .bookInfo .editBtn {
    position: absolute;
    right: 0; }
  .productItem .bookInfo .name {
    font-size: 18px;
    font-weight: bold; }
  .productItem .bookInfo .author {
    font-size: 14px; }
  .productItem .bookInfo .rate i {
    font-size: 11px;
    color: #ffb307;
    margin-right: 2px; }
  .productItem .bookInfo .desc {
    font-size: 13px;
    line-height: 17px;
