.product-fade-enter {
  opacity: 0.1; }

.product-fade-enter-active {
  opacity: 1;
  transition: all 0.3s ease-in; }

.product-fade-exit {
  opacity: 1; }

.product-fade-exit-active {
  opacity: 0.1;
  transition: all 0.3s ease-in; }

.addProductPopup {
  width: 600px;
  padding: 0 2rem 2rem 2rem; }
  .addProductPopup .title {
    font-size: 22px; }

.panel-wrapper {
  position: relative;
  display: none; }
  .panel-wrapper .over-layer {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.4);
    z-index: 9; }
  .panel-wrapper .panel {
    position: fixed;
    top: 5rem;
    background: #fff;
    box-shadow: -3px 0 3px rgba(0, 0, 0, 0.04);
    z-index: 99;
    left: 50%;
    margin-left: -300px; }
    .panel-wrapper .panel .head {
      padding: 0.5rem;
      text-align: right; }
      .panel-wrapper .panel .head i {
        font-size: 25px; }

.panel-wrapper.active {
  display: block; }

.cart-page {
  max-width: 1100px;
  margin: auto;
  margin-top: 1rem;
  padding: 1rem; }
  .cart-page .cart-title {
