.cart-item-enter {
  opacity: 0.1;
  transition: all 0.3s ease-in; }

.cart-item-enter.cart-item-enter-active {
  opacity: 1; }

.cart-item-exit {
  opacity: 1;
  transition: all 0.3s ease-in; }

.cart-item-exit.cart-item-exit-active {
  opacity: 0.1; }

.userInfoWrapper {
  position: relative; }
  .userInfoWrapper .userName {
    cursor: pointer; }
    .userInfoWrapper .userName i {
